import Vue from 'vue'
import Router from 'vue-router'
const gaa = () => import('@/components/zhuye/gaa')
const wu = () => import('@/components/zhufu/wu')
const hui = () => import('@/components/zhufu/hui')
const denglu = () => import('@/components/2jilu/denglu')
const Jazai = () => import('@/components/zhuye/lajia')
const xiang = () => import('@/components/xiang/xiang-1')
const luntiao = () => import('@/components/xiang/luntiao')
const wode= () => import('@/components/zhufu/wode')
const wodezi= () => import('@/components/2jilu/wodezi')
const shouhuo= () => import('@/components/2jilu/shouhuo')
const shangchuan= () => import('@/components/2jilu/genghuan')
const shouhuozi= () => import('@/components/2jilu/shouhuozi')
const wath= () => import('@/components/yeyu/wath')
const guancun= () => import('@/components/yeyu/guancun')
const ditu= () => import('@/components/diti/ditu')
const  xiadan = () => import('@/components/xiang/xiadan')
const  list = () => import('@/components/xiang/List')
const  gouwu = () => import('@/components/zhufu/Ding')
const jiqiren = () => import('@/components/diti/jiqiren')
const zidingyi =()=>import('@/components/yeyu/zidingyi')
const chacao =()=>import('@/components/yeyu/chacao')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'gaa',
      component: gaa,
     
    },
     {
      path: '/wu',
      name: 'wu',
      component: wu,
    },
    {
      path: '/hui',
      name: 'hui',
      component: hui
    },
    {
      path:'/denglu',
      name:'denglu',
      component:denglu
    },
    {
      path:'/Jazai',
      name:'Jazai',
      component:Jazai
    },
    {
      path:'/xiang',
      name:'xiang',
      component:xiang
    },
    {
      path:'/luntiao',
      name:'luntiao',
      component:luntiao
    },
    {
      path:'/wode',
      name:'wode',
      component:wode
    },
    {
      path:'/wodezi',
      name:'wodezi',
      component:wodezi
    },
    {
      path:'/shouhuo',
      name:'shouhuo',
      component:shouhuo 
    },
      {
        path:'/shangchuan',
        name:'shangchuan',
        component:shangchuan 
      },
      {
        path:'/shouhuozi',
        name:'shouhuozi',
        component:shouhuozi 
      },
      {
        path:'/gouwu',
        name:'gouwu',
        component:gouwu 
      },
      {
        path:'/wath',
        name:'wath',
        component:wath,
        children:[
          {
            path:'/wath/guancun',
            component:guancun,
            meta:{ keepAlive:true}
          }
        ]
      },
      {
        path:'/ditu',
        name:'ditu',
        component:ditu 
      },
      {
        path:'/xiadan',
        name:'xiadan',
        component:xiadan 
      },
      {
        path:'/list',
        name:'list',
        component:list 
      },
      {
        path:'/jiqiren',
        name:'jiqiren',
        component:jiqiren 
      },
      {
        path:'/zidingyi',
        name:'zidingyi',
        component:zidingyi 
      },
      {
        path:'/chacao',
        name:'chacao',
        component:chacao 
      },
  ],
})



