<template>
  <div id="tou">
    <div>
      <router-link to="/hui">
        <span :class="txt"></span>
      </router-link>
      <router-link to="/">{{txt1}}</router-link>
      <router-link to="/wode" v-if="name==true">
        <span class="el-icon-user-solid"></span>
      </router-link>
      <!-- 页面输出父组件传输的值 -->
      <!-- <p @click="faA">{{inputName}}</p> -->
      <router-link to="/denglu" v-if="name==false">登陆注册</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  // 子组件接受
  props: ["inputName"],

  data() {
    return {
      list: [],
      txt: "el-icon-zoom-in",
      txt1: "主页面",
      name: true
    };
  },
  mounted() {
    if (localStorage.vuex) {
      this.name = true;
    } else {
      this.name = false;
    }
  },
  beforeMount() {
    if (localStorage.biao && localStorage.zhu) {
      this.txt = localStorage.biao;
      this.txt1 = localStorage.zhu;
    }
  },
  methods: {
    faA() {
      console.log(this.txt1);
      // 父子传值
      this.$emit("faq", this.txt1);
    }
  }
};
</script>
<style>
#tou {
  width: 100%;
  height: 1.5rem;
}

#tou > div {
  width: 100%;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: fixed;
  top: 0;
  background: #3190e8;
  z-index: 5;
}
</style>


