import Vue from 'vue';
import Vuex from 'vuex';
import name from './Store-name' 
import VuexPersistence from 'vuex-persist'
const vuexLocal = new VuexPersistence({
    storage: window.localStorage
})
Vue.use(Vuex);

const store = new Vuex.Store({
    state:{
        arr:123,
         name:'',
         txt1:'',
         txt2:'',
         obj:[]
    },
    //数据的二次处理
    getters:{
      fetArr:(state)=>{
          return  state.arr+=2
      },
      
    },
    mutations:{
        faA(state,value){
            state.arr=value
        },
        fab(state,value){
            state.name=value
        },
        faC(state,value){
            state.txt1=value
        },
        faD(state,value){
            state.txt2=value
        },
        faE(state,value){
            state.obj=value
        }

    },
    actions:{
        getname(constter){
           
        }
        // 使用方法 this.#store.dispatch('方法名'，参数)
    },
     plugins: [vuexLocal.plugin]
    // modules:{
    //     name,
    // }
});
 
export default store;


